// it's really just utilities for Promises

export class Completer<T> {
  promise: Promise<T>;

  resolve: (value?: T) => void;
  reject: (err?: any) => void;

  constructor() {
    this.promise = new Promise((resolve, reject) => {
      this.resolve = resolve;
      this.reject = reject;
    });
  }
}

export function ok<T>(cb: (_: (err?: any, value?: any) => void) => void): Promise<T> {
  const completer = new Completer<T>();

  cb((err, value) => {
    if (!!err) {
      completer.reject(err);
      return;
    }

    completer.resolve(value);
  });

  return completer.promise;
}

// resolveImmediate function modified from fbjs <3

const resolvedPromise = Promise.resolve();

function throwNext(error) {
  setTimeout(() => {
    throw error;
  }, 0);
}

/**
 * An alternative to setImmediate based on Promise.
 */
export function resolveImmediate(callback: () => any): Promise<void> {
  return resolvedPromise.then(callback).catch(throwNext);
}